<style scoped lang="less">
  /* 头部 */
  .el-header {
    position: relative;
    top: 0;left: 0;
    background-color: #B3C0D1;
    color: #333;
    line-height: 60px;
    text-align: right;
    font-size: 12px;

    .title{
      position: absolute;
      top: 0;left: 30px;
      font-size: 20px;
      color: #fff;
    }
  }

  /* 主体 */
  .container{
    height: 100%;
    border: 1px solid #eee;

    // 左侧导航
    .left{
      position:fixed;
      top:0;left:0;
      height: 100%;
    }

    .tool-fix{
      display: flex;
      align-items: center;
      position: fixed;
      bottom: 50px;
      left: 50px;
      height: 30px;
      padding: 10px;
      background: #fff;
      box-shadow: 0 0 3px rgba(0,0,0,0.2);
      border-radius: 4px;
      box-sizing: border-box;
    }
  }
</style>

<template>
  <!-- 主体 -->
  <el-container class="container">
    <!-- 左侧内容 -->
    <el-aside class="left" width="200px">
      <el-menu router
               @select="selectMenu"
               default-opened="8"
               style="overflow-x:hidden;height: 100%;"
               :default-active="$route.path"
               background-color="#2f4050"
               text-color="#fff"
               active-text-color="#ffd04b">
        <!-- 子菜单 -->
        <el-submenu v-for="(item, index) in nav" :index="index + ''" :key="'-' + index">
          <template slot="title">{{item.title}}</template>
          <el-menu-item-group v-for="(item2, index2) in item.child" :key="index + '--' + index2">
            <el-menu-item :index="item2.path || index + '-' + index2">{{item2.title || item2}}</el-menu-item>
          </el-menu-item-group>
        </el-submenu>
      </el-menu>
    </el-aside>
    <div style="overflow-x: hidden;flex: 1;margin-left: 200px;">
      <!-- 头部 -->
      <el-header style="background: #283847;">
        <div class="title">
          {{selectPage}}
        </div>
        <el-dropdown @command="showEditPswDialog">
          <i class="el-icon-setting" style="margin-right: 15px;color: #fff;"></i>
          <el-dropdown-menu slot="dropdown">
            <el-dropdown-item command="0"> <i class="el-icon-circle-close"></i>退出</el-dropdown-item>
          </el-dropdown-menu>
        </el-dropdown>
        <span style="color: #fff;">你好, {{user.loginName}}</span>
      </el-header>
      <!-- 右侧内容 -->
      <router-view></router-view>
    </div>
  </el-container>
</template>

<script>
import { APILogOut, getMyInfo } from '@/utils/api'

export default {
  name: 'index-page',
  data () {
    return {
      selectPage: '游戏分包报表',
      nav: [],
      user: {}
    }
  },
  async mounted () {
    try {
      // 获取个人信息
      const res = await getMyInfo()
      const data = await this.$checkData(res.data)

      this.user = data

      localStorage.setItem('user', JSON.stringify(data))

      if (data.loginName === 'admin') {
        this.nav.push({
          title: '系统管理',
          child: [{
            title: '系统用户',
            path: '/systemAdmin'
          }]
        })
      }

      this.nav.push({
        title: '功能列表',
        child: [{
          title: '游戏分包报表',
          path: '/'
        }]
      })
    } catch (e) {
      window.location.href = '/login.html'
    }
  },
  methods: {
    // 菜单选中事件
    selectMenu (e) {
      this.nav.some(v => {
        return v.child.some(v1 => {
          if (v1.path === e) {
            this.selectPage = v1.title
            return true
          }
        })
      })
    },
    // 显示密码编辑框
    async showEditPswDialog (index) {
      try {
        if (index === '0') {
          // 退出登录
          await APILogOut()
          // 跳转到login.html页面
          window.location.href = '/login.html'
        } else if (index === '1') {
        }
      } catch (e) {
        console.log(e)
      }
    }
  }
}
</script>
