export function checkData (data) {
  return new Promise((resolve, reject) => {
    if (data.ErrorMsg) {
      this.$message({
        showClose: true,
        message: data.ErrorMsg,
        type: 'error'
      })
      // 错误情况
      reject(data.ErrorMsg)
    } else {
      resolve(data)
    }
  })
}

