export function getMyRoleNav (data) {
  const tree = sortMenuTree(data)
  const result = []
  roleNavs.forEach(r => {
    const d1 = {
      title: r.title,
      child: []
    }

    tree.some(t => {
      if (r.title === t.label) {
        r.child.forEach(r1 => {
          t.children.some(t1 => {
            if (r1.title === t1.label) {
              d1.child.push(r1)
              return true
            }
          })
        })
      }
    })

    if (d1.child.length === 0) return

    result.push(d1)
  })

  return result
}

export function sortMenuTree (treeData, filter = false) {
  // treeData 权限树 数组  id label children
  const M = []
  const C = []
  const F = []

  treeData.forEach(v => {
    if (!v.chcked && filter) return
    if (v.type === 'M') {
      M.push({
        id: v.id,
        label: v.name,
        children: [],
        checked: v.chcked
      })
    } else if (v.type === 'C') {
      C.push({
        id: v.id,
        label: v.name,
        children: [],
        pId: v.pId,
        checked: v.chcked
      })
    } else if (v.type === 'F') {
      F.push({
        id: v.id,
        label: v.name,
        children: [],
        pId: v.pId,
        checked: v.chcked
      })
    }
  })

  C.forEach(c => {
    F.forEach(f => {
      if (f.pId === c.id) {
        c.children.push(f)
      }
    })
  })

  M.forEach(m => {
    C.forEach(c => {
      if (c.pId === m.id) {
        m.children.push(c)
      }
    })
  })
  return M
}

