const XWWWPOST = {
  transformRequest: [function (data) {
    let ret = ''
    for (let it in data) {
      ret += encodeURIComponent(it) + '=' + encodeURIComponent(data[it]) + '&'
    }
    return ret
  }],
  headers: {
    'Content-Type': 'application/x-www-form-urlencoded'
  }
}

// 导出
export function exportTableData (start = null, end = null) {
  window.open(`subcontract/exportListGameSub.do?start=${start}&end=${end}`)
}

// 获取个人信息
export function getMyInfo () {
  return axios.get('getUser.do')
}

// 退出登录
export function APILogOut () {
  return axios.get('logout.do')
}

// 获取用户管理员列表
export function userAdmin (page, size) {
  return axios.get(`user/findPage.do?page=${page}&rows=${size}`)
}

// 新增用户
export function addNewUserAdmin (user) {
  return axios({
    method: 'POST',
    url: `user/saveUser.do`,
    data: user,
    ...XWWWPOST
  })
}

// 新增channelIdAPI
export function addNewChannelId (id, channelId) {
  return axios({
    method: 'POST',
    ...XWWWPOST,
    url: '/user/addChannelId.do',
    data: {
      id,
      channelId
    }
  })
}

// 加载游戏分包报表数据
export function gameProfitNews (page, rows) {
  return axios({
    method: 'get',
    url: 'subcontract/findPage.do',
    params: {
      page,
      rows
    }
  })
}

// 查询游戏分包报表数据
export function searchGameProfitNews (page, rows, start = null, end = null) {
  return axios({
    method: 'get',
    url: 'subcontract/findSearch.do',
    params: {
      page,
      rows,
      start,
      end
    }
  })
}

// 删除游戏分包报表数据
export function deleteGameProfitNews (ids) {
  return axios({
    method: 'get',
    url: '/subcontract/deleteSubContract.do',
    params: {
      ids
    }
  })
}

// 修改游戏分包报表数据
export function editProfitNews (data) {
  return axios({
    method: 'POST',
    url: '/subcontract/updateSubContract.do',
    ...XWWWPOST,
    data
  })
}

// 上传文件
export async function uploadFile (obj) {
  let file = obj.file
  let param = new FormData() // 创建form对象
  param.append('filename', file)// 通过append向form对象添加数据

  let config = {
    headers: { 'Content-Type': 'multipart/form-data' }
  } // 添加请求头

  return axios.post('subcontract/importSubContract.do', param, config)
}
